import { useState, useEffect, useRef } from "react";


export default function Price(){
    const ref = useRef();
    const [scroll, setScroll] = useState(0)
    const [rects, setRects] = useState(null)

    useEffect(() => {
        const rect = ref.current.getBoundingClientRect();
        handleRef(rect.y)
      });
      const handleRef = (y) => {
        setRects(y);
        console.log(rects);
    };
    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);
    const handleScroll = () => {
        setScroll(window.scrollY);
    };

    const divStyle = {
        transform: `translateX(${(rects - scroll/5.5)}px)`,
      };

    return(
        <div className="price">
            <div className="price_text-wrap">
                <div className="text-wrap_text" ref={ref} style={divStyle}>
                    <p> </p> <p>   </p>  <p>   </p>
                </div>    
            </div>
            <div className="price_price-wrap">
                    <div className="price_price-string">
                        <p>UI/UX </p> <p> 10.000</p> <p><b> :</b> 7 </p>
                    </div>
                    <div className="price_price-string">
                        <p>Landing Page</p> <p> 6.000</p> <p><b> :</b> 7 </p>
                    </div>
                    <div className="price_price-string">
                        <p>-</p> <p> 15.000</p> <p><b> :</b> 12 </p>
                    </div>
                    <div className="price_price-string">
                        <p> </p> <p> 6.000</p> <p><b> :</b> 10 </p>
                    </div>
                    <div className="price_price-string">
                        <p> </p> <p> 2.000</p> <p><b> :</b> 3 </p>
                    </div>
                </div>
        </div>
    )
}